.courses {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.courses-click-style {
  min-height: calc(80vh - 9.5rem);
}

.courses h2 {
  margin: 2rem 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.courses .courses-card:hover .text-box {
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.courses .courses-card .img-box {
  overflow: hidden;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.courses .courses-card:hover .img-box img {
  transform: scale(1.08);
}

.courses .courses-card .img-box img {
  width: 100%;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  transition: all 0.2s linear;
}

.courses .courses-card .text-box {
  padding: 1.6rem;
  background-color: #edf4f6;
  transition: all 0.2s linear;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.courses .courses-card .text-box h3 {
  font-size: 17px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: 1rem;
}

.courses .courses-card .text-box .card-text {
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 1rem;
}

.courses .courses-card .text-box button {
  border: none;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  padding: 0.5rem 1rem;
  background-color: #226500;
  color: #fff;
  border-radius: 4px;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  transition: all 0.15s linear;
}

.courses .courses-card .text-box button:hover {
  transform: translateY(-4px);
}

.courses .courses-card .text-box button:active {
  transform: translateY(-2px);
}

@media screen and (max-width: 767px) {
  .courses {
    align-items: center;
    padding: 1.5rem;
  }
  .courses .courses-card .text-box button {
    font-size: 13px;
  }
}
